import { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import Image from 'components/Image';
import Footer from 'components/Footer';
import {
    ProjectContainer,
    ProjectBackground,
    ProjectHeader,
    ProjectSection,
    ProjectSectionContent,
    ProjectImage,
    ProjectSectionHeading,
    ProjectSectionColumns,
    ProjectTextRow,
    ProjectSectionText,
} from 'components/ProjectLayout';
import { useScrollRestore } from 'hooks';
import { media } from 'utils/style';
import prerender from 'utils/prerender';
import mkrisBG from 'assets/mkrisBG.png';
import mkrisSite from 'assets/mkrisSite.png';
import blog from 'assets/blog.png';
import blogSide from 'assets/blogSide.svg';

const title = 'M-Kris Website';
const description =
    'Design and development of the main company website for M-Kris Electric with full functionality of Blogs.';
const roles = [
    'Front End Development',
    'Minimal Backend Development',
    'Developed using ReactJs and Nodejs',
    'Minimal Design'
];

const ProjectDM = () => {
    useScrollRestore();

    return (
        <Fragment>
            <Helmet>
                <title>Projects | {title}</title>
                <meta name="description" content={description} />
            </Helmet>
            <ProjectContainer>
                <ProjectBackground
                    srcSet={`${mkrisBG} 1080w, ${mkrisBG} 2160w`}
                    placeholder={mkrisBG}
                    entered={!prerender}
                />
                <ProjectHeader
                    title={title}
                    description={description}
                    url="https://mkris.com/"
                    roles={roles}
                />
                <ProjectSection first>
                    <ProjectSectionContent>
                        <ProjectImage
                            raised
                            srcSet={`${mkrisSite} 1280w, ${mkrisSite} 2560w`}
                            placeholder={mkrisSite}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 800px, 1000px`}
                            alt="Device Models plugin interface."
                        />
                    </ProjectSectionContent>
                </ProjectSection>
                <ProjectSection>
                    <ProjectTextRow>
                        <ProjectSectionHeading>The Aim</ProjectSectionHeading>
                        <ProjectSectionText>
                            To Create a fully functional, and a highly reactive website for
                            M-Kris Electric which not only flows efficiently and in a fluid like
                            nature but also showcases all the important aspects the company aspires
                            to be and to showcase all the products the which are currently in
                            production
                        </ProjectSectionText>
                        <ProjectSectionText>
                            The project was especially tough for me because creating a fully functional
                            website was a new task and to incorporate all the new practises was a major task
                            which was overcome by constant learning and never backing off.
                        </ProjectSectionText>
                        <ProjectSectionText>
                            The website also features a fully functional blog designing which is only limited
                            to a certain people.
                        </ProjectSectionText>
                    </ProjectTextRow>
                </ProjectSection>
                <ProjectSection light>
                    <ProjectSectionColumns>
                        <ProjectTextRow>
                            <ProjectSectionHeading>The Blog</ProjectSectionHeading>
                            <ProjectSectionText>
                                The website also features a fully working blogs wherein different 
                                types of blogs can be showcased based on their categories and their 
                                authors.
                            </ProjectSectionText>
                            <ProjectSectionText>
                                Currently the blog is limited to a very few people. Opening the Blog 
                                to public would be a future task.
                            </ProjectSectionText>
                        </ProjectTextRow>
                        <Image
                            srcSet={`${blogSide} 400w, ${blogSide} 898w`}
                            placeholder={blogSide}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 100vw, 50vw`}
                            alt="The Device Models color palette and logo, featuring a low poly monogram to convey its 3D allure."
                        />
                    </ProjectSectionColumns>
                </ProjectSection>
                <ProjectSection>
                    <ProjectSectionContent>
                        <Image
                            srcSet={`${blog} 400w, ${blog} 898w`}
                            placeholder={blog}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 100vw, 50vw`}
                            alt="The Device Models color palette and logo, featuring a low poly monogram to convey its 3D allure."
                        />
                    </ProjectSectionContent>
                </ProjectSection>
            </ProjectContainer>
            <Footer />
        </Fragment>
    );
};

export default ProjectDM;
